 */

 .wp-block-link-block-link-block{
	height: 100%;	
 }

 .wp-block-link-block-link-block a{
	display: flex;
	display: -webkit-flex;
	text-decoration: none !important;
	height: 100%;
}
.wp-block-link-block-link-block a:hover{
	text-decoration: none !important;
}
.wp-block-link-block-link-block a>div{
	padding: var(--wp--preset--spacing--20);
}
.wp-block-link-block-link-block a .IconBar{
	grid-column: 1/span 1;
	// background-color:var(--wp--preset--color--accent);
	border-width: 0px 1px 0px 0px;
	border-style: solid;
	border-color: var(--wp--preset--color--contrast);
	width: 2.5rem;
	flex-grow: 0;
	flex-shrink: 0;
	transition: padding 0.5s cubic-bezier(.97,-0.27,.42,1.32);
}
.wp-block-link-block-link-block:hover a .IconBar{
	// padding-top: 50%;
	transition: padding 0.5s cubic-bezier(.51,0,.03,1.28);
}
.wp-block-link-block-link-block a .IconBar img{
	max-width: 100%;
	transform: rotate(0deg);
	transform-origin: center;
	aspect-ratio: 1/1 !important;
	transition:
		transform 0.5s cubic-bezier(.97,-0.27,.42,1.32),
		top 0.5s cubic-bezier(.97,-0.27,.42,1.32);
	position: relative;
	top:0%;
}
.wp-block-link-block-link-block:hover a .IconBar img{
	top:50%;
	transform: rotate(360deg);
	transition:
		transform 0.5s cubic-bezier(.51,0,.03,1.28),
		top 0.5s cubic-bezier(.51,0,.03,1.28);
}
.wp-block-link-block-link-block a .BlockCopy{
	grid-column: auto/span 4;
	display: flex;
	display: -webkit-flex;
	flex-direction: column;
	-webkit-flex-direction:column;
	gap:var(--wp--preset--spacing--40);
	flex-grow: 1;
	justify-content: space-between;
	-webkit-justify-content:space-between;
}
.wp-block-link-block-link-block a .BlockCopy *{
	margin: 0px;
}

@media screen and (max-width:768px) {
	.wp-block-link-block-link-block a{
		flex-direction:row-reverse;
	}
	.wp-block-link-block-link-block a .IconBar{
		border-width: 0px 0px 0px 1px;

	}
}

